<template>
  <div class="bookstore">
    <loading v-show="isLoading"/>
    <van-nav-bar>
      <template #left>
        <div class="logo">
          <img class="auto-img" :src="require('../../assets/images/logo.png')" alt />
        </div>
      </template>
    </van-nav-bar>
    <van-search
      v-model="value"
      placeholder="请输入书名或者作者名"
      background="#fff"
      @focus="goPage('Search')"
    />
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item
        v-for="(item , index) in bannerData"
        :key="index"
        @click="goPage('Book',item.id)"
      >
        <img class="auto-img banner-img" v-lazy="
          item.url" alt="图片未加载..." />
      </van-swipe-item>
    </van-swipe>
    <div class="content-box" v-for="(val,i) in titleType" :key="i">
      <div class="text-title">{{val.title}}</div>
      <div
        class="item clearfix"
        v-for="(v,j) in titleType[i].data"
        :key="j"
        @click="goPage('Book',v._id)"
      >
        <div class="img-box fl">
          <img class="auto-img" v-lazy="getImg(v.cover)" alt="图片未加载..." />
        </div>
        <div class="text-box fl">
          <div class="title">{{v.title}}</div>
          <div class="text">{{v.shortIntro}}</div>
          <div class="type-box">
            <span>{{v.minorCate}}</span>
            <span class="num">{{v.latelyFollower | num}}万人气</span>
            <span class="type">{{v.majorCate}}</span>
          </div>
        </div>
      </div>
    </div>
    <van-divider
      :style="{ color: 'lightsteelblue', borderColor: 'lightsteelblue', padding: '0 16px' }"
    >我是有底线的</van-divider>
    <div class="ranking-box" @click="goPage('Ranking')">
      <svg
        t="1595754594569"
        viewBox="0 0 1024 1024"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        p-id="2572"
        width="28"
        height="28"
      >
        <path
          d="M0.768 321.472v637.504c0 35.264 28.16 64.128 62.72 64.128h205.248V256.832H64.832c-35.392 0-64.064 29.056-64.064 64.64z m151.744 26.88a21.504 21.504 0 0 0-16.192-6.144 19.712 19.712 0 0 0-11.456 3.2 22.4 22.4 0 0 0-7.424 8.32 40.704 40.704 0 0 0-3.968 11.392c-0.832 4.032-1.088 8.32-1.088 12.352H83.2a80.512 80.512 0 0 1 2.944-24.512 61.312 61.312 0 0 1 10.368-19.904 47.168 47.168 0 0 1 17.28-13.248 55.872 55.872 0 0 1 23.936-4.8c6.912 0 13.504 1.088 19.648 3.2 6.4 2.176 11.712 5.376 16.512 9.408a42.048 42.048 0 0 1 11.136 15.104 44.992 44.992 0 0 1 4.288 20.224 49.152 49.152 0 0 1-13.312 34.752c-3.968 4.288-8.512 8.32-13.568 11.84-5.056 3.52-10.112 7.04-15.36 10.496-5.12 3.52-10.176 7.296-15.232 11.072a79.36 79.36 0 0 0-12.992 13.696h71.232v26.944H79.68c0-8.896 1.28-16.64 3.712-23.424 2.432-6.464 5.888-12.416 10.112-17.792a86.4 86.4 0 0 1 15.168-14.272c5.824-4.288 11.968-8.896 18.368-13.44 3.2-2.432 6.912-4.864 10.624-7.296a60.992 60.992 0 0 0 10.112-8.064 39.872 39.872 0 0 0 7.68-9.984 24.832 24.832 0 0 0 3.2-12.672 21.44 21.44 0 0 0-6.144-16.384zM637.184 0.128H386.24c-34.56 0-62.72 28.8-62.72 64.384v959.104h376.64V64.512C699.904 28.928 671.744 0.128 637.184 0.128z m-101.056 212.48H505.6v-99.84h-37.76v-23.488a72.96 72.96 0 0 0 15.36-1.088 37.76 37.76 0 0 0 23.424-14.528A35.072 35.072 0 0 0 512 58.816h24.192v153.856zM958.592 459.648h-203.904v563.968h205.184c34.56 0 62.72-28.8 62.72-64.064V524.8c0-35.84-28.672-65.152-64-65.152z m-18.368 189.568a48.448 48.448 0 0 1-12.48 15.936 52.032 52.032 0 0 1-17.856 9.664c-6.592 2.176-13.824 3.2-20.992 3.2-8.512 0-16.192-1.28-23.104-3.712a51.968 51.968 0 0 1-17.792-10.816 49.728 49.728 0 0 1-11.456-17.472 60.8 60.8 0 0 1-3.712-23.68h28.992a57.408 57.408 0 0 0 1.856 11.84c1.024 3.776 2.88 7.04 5.056 9.664a23.552 23.552 0 0 0 19.392 8.96c6.912 0 12.8-2.24 17.536-6.528a22.72 22.72 0 0 0 7.168-17.792 21.888 21.888 0 0 0-3.456-13.44 19.2 19.2 0 0 0-8.512-6.72 37.632 37.632 0 0 0-11.136-2.432c-3.968-0.256-8-0.256-11.712-0.25