          fill="#B0C4DE"
          p-id="2573"
        />
      </svg>
      <span class="ranking-text">排行榜</span>
    </div>
  </div>
</template>

<script>
import  loading  from "../../components/loading";
import "../../assets/css/bookstore.less";
export default {
  components:{
    loading
  },
  data() {
    return {
      value: "",
      // 轮播图数据
      bannerData: [
        {
          id: "5e218eca6c4f4f748c380e3b",
          url: require("../../assets/images/1.jpg")
          // 熊猫
        },
        {
          id: "53e020642ad91059430caddf",
          url: require("../../assets/images/2.jpg")
          // 捡到一条龙
        },
        {
          id: "5d5cf87f67621e4f2d38640e",
          url: require("../../assets/images/3.jpg")
          // 炼妖壶
        },
        {
          id: "5def4e03614600d7f471dcbf",
          url: require("../../assets/images/4.jpg")
          // 调教夫君
        },
        {
          id: "5ee217adf7123364a6a51878",
          url: require("../../assets/images/5.jpg")
          // 剑道
        }
      ],
      // 标题类型
      titleType: [
        {
          title: "男生热门",
          id: "54d42d92321052167dfb75e3",
          data: []
        },
        {
          title: "女生热门",
          id: "54d43437d47d13ff21cad58b",
          data: []
        },
        {
          title: "人气漫画",
          id: "5a322ef4fc84c2b8efaa8335",
          data: []
        }
      ],
      isLoading:true
    };
  },
  created() {
    this.getBook();
  },
  methods: {
    // 跳转到搜索 | 排行榜 页面
    goPage(name, id) {
      if (id) {
        this.$router.push({ name: name, query: { id: id } });
      } else {
        this.$router.push({ name });
      }
    },
    // 带id跳转到书籍详情页
    goBook(id) {
      this.$router.push({ name: "Book", query: { id: id } });
    },
    // 获取书籍数据
    getBook(id) {
      this.axios({
        method: "get",
        url: "http://novel.kele8.cn/rank-category"
      })
        .then(res => {
          if (res.status == 200) {
            for (let i = 0; i < this.titleType.length; i++) {
              this.axios({
                method: "get",
                url: "http://novel.kele8.cn/rank/" + this.titleType[i].id
              })
                .then(res => {
                  this.isLoading = false;
                  this.titleType[i].data = res.data.ranking.books.slice(0, 5);
                })
                .catch(err => {this.isLoading = false;});
            }
          }
        })
        .catch(err => {});
    },
    // 图片解码
    getImg(val) {
      return unescape(val).slice(7);
    }
  }
};
</script>
